<template>
  <div class="detailsku">
    <!--  -->
    <van-sku
      v-model="showSku"
      :sku="sku"
      :goods="goods"
      :goods-id="goodsId"
      :quota="quota"
      :quota-used="quotaUsed"
      :hide-stock="sku.hide_stock"
      :close-on-click-overlay="true"
      safe-area-inset-bottom
      show-add-cart-btn
      reset-stepper-on-hide
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
    ></van-sku>
  </div>
</template>

<script>
import Qs from "qs";
export default {
  name: "detailsku",
  data() {
    return {
      uid: 956538, //id
      skuid: null, //id
      detaildata: {}, //
      showSku: false, //sku
      goodsId: 23444, //id
      quota: 0, // 0
      quotaUsed: 345, //
      sku: {
        // 
        tree: [
          {
            k: "", // skuKeyName
            k_id: 1,
            v: [
              {
                id: "1214", // skuValueId id
                name: "", // skuValueName
                imgUrl:
                  "http://t00img.yangkeduo.com/goods/images/images/2019-10-05/a886e0b1-f10d-46bd-bc5c-9ca19f9cbc09.jpg", // 
                previewImgUrl:
                  "http://t00img.yangkeduo.com/goods/images/images/2019-10-05/a886e0b1-f10d-46bd-bc5c-9ca19f9cbc09.jpg" // 
              },
              {
                id: "1215",
                name: "",
                imgUrl:
                  "http://t00img.yangkeduo.com/goods/images/images/2019-10-05/a886e0b1-f10d-46bd-bc5c-9ca19f9cbc09.jpg",
                previewImgUrl:
                  "http://t00img.yangkeduo.com/goods/images/images/2019-10-05/a886e0b1-f10d-46bd-bc5c-9ca19f9cbc09.jpg"
              }
            ],
            // skuKeyStrsku  list key value  id
            k_s: "s1"
          }
          // {
          //   k: "",
          //   k_id: 2,
          //   v: [
          //     {
          //       id: "1194",
          //       name: "S"
          //     },
          //     {
          //       id: "1195",
          //       name: "M"
          //     },
          //     {
          //       id: "1196",
          //       name: "L"
          //     }
          //   ],
          //   k_s: "s2"
          // }
        ],
        //  sku M  sku S 
        list: [
          //  
          /*{
            id: 2259, // skuId
            price: 100, // 
            s1: "1214", //  k_s  s1  id
            s2: "1194", //  k_s  s2  id
            // s3: "0", // 30
            stock_num: 110 //  sku 
          }
          {
            id: 2259,
            price: 129,
            s1: "1214",
            s2: "1195",
            stock_num: 130
          },
          {
            id: 2259,
            price: 129,
            s1: "1214",
            s2: "1196",
            stock_num: 130
          },
          // 
          {
            id: 2259,
            price: 129,
            s1: "1215",
            s2: "1194",
            stock_num: 130
          },
          {
            id: 2259,
            price: 129,
            s1: "1215",
            s2: "1195",
            stock_num: 130
          },
          {
            id: 2259,
            price: 129,
            s1: "1215",
            s2: "1196",
            stock_num: 130
          }*/
        ],
        price: "9.68-15.96", // 
        stock_num: 227, // 
        collection_id: 2261, //  skuId  collection_id sku  id
        none_sku: false, // 
        hide_stock: false //
      },
      goods: {
        //
        title: "vivooppo",
        picture:
          "http://t00img.yangkeduo.com/goods/images/2019-10-26/757305fa0ef438882a0d3db39cebcda3.jpeg?imageMogr2/sharpen/1%7CimageView2/2/w/300/q/70/format/webp"
      }
    };
  },
  methods: {
    onBuyClicked(skuData) {
      if (this.$store.state.userToken) {
        this.$toast.loading({ className: "myToast", forbidClick: true });
        setTimeout(() => {
          this.$toast.clear();
          let data = {
            userid: this.uid,
            active_type: 0,
            goods_id: skuData.goodsId,
            sku_id: this.skuid,
            goods_num: skuData.selectedNum,
            use_balance: 0,
            use_integral: 0,
            use_auction_integral: 0,
            is_pei: 0,
            pf: 1
          };
          this.$router.push({ name: "submitorder", params: { data } });
        }, 300);
      } else {
        this.$router.push("/login");
      }
    },
    onAddCartClicked(skuData) {
      if (this.$store.state.userToken) {
        let params = {};
        if (skuData.selectedSkuComb !== undefined && this.detaildata) {
          this.axios
            .post(
              "https://api.ymduo.com/item/attr",
              Qs.stringify({
                gid: skuData.goodsId,
                attr: skuData.selectedSkuComb.name
              })
            )
            .then(res => {
              this.skuid = res.data.result.data[0].sku_id;
            });
          params = {
            user_id: this.uid,
            shop_id: this.detaildata.storeInfo.store_id,
            goods_id: skuData.goodsId,
            sku_id: this.skuid,
            goods_num: skuData.selectedNum,
            active_type: 0
          };
        } else if (this.detaildata) {
          params = {
            user_id: this.uid,
            shop_id: this.detaildata.storeInfo.store_id,
            goods_id: skuData.goodsId,
            goods_num: skuData.selectedNum,
            active_type: 0
          };
        }
        if (this.detaildata) {
          this.axios
            .post("https://api.ymduo.com/cart/add", Qs.stringify(params))
            .then(() => {
              // console.log(res);
              this.showSku = false;
              this.$toast.success("");
              this.axios
                .post(
                  "https://api.ymduo.com/Home/Cart/cartnum",
                  Qs.stringify({
                    uid: this.uid
                  })
                )
                .then(res => {
                  if (res.data.code == 200) {
                    this.$emit("cartnum", res.data.result.data);
                  }
                });
            });
        }
      } else {
        this.$router.push("/login");
      }
    }
  }
};
</script>

<style lang="less" scoped>
.detailsku {
  width: 100%;
}
</style>
